.nbp-spinner-wrapper {
  position: fixed;
  width: 100%;
  height:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  top: 0;
  left: 0;
  z-index: 99999;
}

.nbp-spinner-overlay {
  position: fixed;
  width: 100%;
  height:100%;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  background-color:rgba(255, 255, 255, 0.8);
  z-index: 99998;
}

.nbp-spinner {
  display: flex;
  width: 90px;
  height: 90px;
  background-image:url(/cjlib/assets/images/spinner.svg);
  margin: 0 auto;
}

.nbp-message {
  display: flex;
  margin: 0 auto;
  font-size: 1.5em;
  font-weight: 500;
  white-space: pre-line;
  text-align: center;
  margin-bottom: 40px;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .nbp-spinner {
    background-image:url(/cjlib/assets/images/ISP-loading-spinner.gif) !important;
  }
}